import {
    LitElement,
    html,
    css
} from 'lit-element';
import '@vaadin/vaadin-ordered-layout/vaadin-horizontal-layout';
import '@vaadin/vaadin-ordered-layout/vaadin-vertical-layout';
import '@vaadin/vaadin-button/vaadin-button';
import store from '../redux/store';
import { connect } from 'pwa-helpers/connect-mixin';
import {
    defineComponent
} from '../helper/components-definer';

export default class FeedItem extends connect(store)(LitElement) {
    constructor() {
        super();
    }
    static get name() {
        return 'feed-item';
    }
    static get styles() {
        return css `
            vaadin-horizontal-layout > * {
                margin-right: 20px;
            }
        `;
    }
    static get properties() {
        return {
            myProp: String
        };
    }
    render() {
        return html `
                <vaadin-vertical-layout>
                    <vaadin-horizontal-layout>
                        <img src="https://avatars2.githubusercontent.com/u/21265830?s=460&v=4" width="50px">
                        <p>
                            Gary Valverde
                        </p>
                    </vaadin-horizontal-layout>
                    <p>
                        Redux is cool
                    </p>
                    <div style="text-align:center; width:100%">
                        <img src="https://static-cdn.123rf.com/images/v5/index-thumbnail/84170952-b.jpg" style="max-width: 100%; margin-bottom: 10px">
                    </div>                    
                    <vaadin-horizontal-layout>
                        <vaadin-button  theme="success">Like (514)</vaadin-button>
                        <vaadin-button>Comment (165)</vaadin-button>
                    </vaadin-horizontal-layout>                    
                </vaadin-vertical-layout>
      `;
    }
    stateChanged(state) {
    }
}

defineComponent(FeedItem);