import reducer from './../helpers/reducer';
import {SIGN_UP_SUCCEDED, SIGN_UP_FAILED } from "./types";
import {userSchema} from './../schemas';
import mergeEntities from "../helpers/mergeEntities";

export default [
    reducer(SIGN_UP_SUCCEDED, (base, draft, userData) =>{
        draft.appStatus.signUp.succeded = true;
        draft.appStatus.signUp.error = true;
        const {entities} = normalize(userData, userSchema);
        mergeEntities(draft.entities, entities);
    }),
    reducer(SIGN_UP_FAILED, (base, draft, payload) =>{
        
    })
];