import {
    LitElement,
    html,
    css
} from 'lit-element';
import '@vaadin/vaadin-ordered-layout/vaadin-vertical-layout';
import '@vaadin/vaadin-ordered-layout/vaadin-horizontal-layout';
import '@vaadin/vaadin-button/vaadin-button';
import {
    defineComponent
} from './../helper/components-definer';
import './../components/feed-item';
import store from '../redux/store';
import { connect } from 'pwa-helpers/connect-mixin';

export default class UserProfile extends connect(store)(LitElement) {
    constructor() {
        super();
    }
    static get name() {
        return 'user-profile';
    }
    static get styles() {
        return css `
            vaadin-vertical-layout > *{
                min-width:100%;
            }
            feed-item{
                margin-bottom:50px;
            }
            vaadin-horizontal-layout > * {
                margin-right: 20px;
            }
        `;
    }
    static get properties() {
        return {
            myProp: String
        };
    }
    render() {
        return html `
        <vaadin-horizontal-layout>
            <div style="min-width:25%"></div>
            <vaadin-vertical-layout style="min-width:50%">
                <vaadin-horizontal-layout style="margin-bottom:100px;">
                    <vaadin-vertical-layout>
                        <img src="https://avatars2.githubusercontent.com/u/21265830?s=460&v=4" style="max-width:150px; max-height:150px;">
                        <vaadin-button theme="primary" style="width:100%">
                            Follow                            
                        </vaadin-button>
                    </vaadin-vertical-layout>
                    
                    <vaadin-vertical-layout>
                        <vaadin-horizontal-layout>
                            <p>
                                Gary Valverde
                            </p>
                            <vaadin-button>
                                Edit Profile
                            </vaadin-button>
                        </vaadin-horizontal-layout>

                        <b>Information</b>
                        <span>Male</span>
                        <span>01/01/1997</span>

                        <vaadin-horizontal-layout style="margin-top:20px">
                            <vaadin-button>
                                985 Followers
                            </vaadin-button>
                            <vaadin-button>
                                157 Following
                            </vaadin-button>
                        </vaadin-horizontal-layout>
                    </vaadin-vertical-layout>
                </vaadin-horizontal-layout>
                <vaadin-vertical-layout>
                    <feed-item></feed-item>
                    <feed-item></feed-item>
                    <feed-item></feed-item>
                </vaadin-vertical-layout>
            </vaadin-vertical-layout>
            <div style="min-width:25%"></div>
      `;
    }
    stateChanged(state) {
    }
}

defineComponent(UserProfile);