import { SIGN_UP_REQUEST, SIGN_UP_SUCCEDED, SIGN_UP_FAILED } from "./types";

export const requestSignUp = (user) => ({
    type : SIGN_UP_REQUEST,
    payload : user
});

export const signUpRequestSucceded = (user) => ({
    type : SIGN_UP_SUCCEDED,
    payload : user
});

export const signUpRequestFailed = () => ({
    type : SIGN_UP_FAILED    
});

