import {
    take,
    put,
    takeEvery,
    all,
    call,
    fork,
    takeLatest
} from 'redux-saga/effects';
import {
    SIGN_UP_REQUEST, SIGN_UP_SUCCEDED
} from './types';
import saga from '../helpers/saga';   
import { saveUser } from './../../helper/api';
import { signUpRequestSucceded, signUpRequestFailed } from './actions';

export default [
    saga(takeLatest, SIGN_UP_REQUEST, function*(userData){
        try {
            const user = yield call(saveUser, userData);
            yield put(signUpRequestSucceded(user));
        } catch (e) {
            yield put(signUpRequestFailed());
        }
    })
]
