import {
    LitElement,
    html,
    css
} from 'lit-element';
import {
    defineComponent
} from './../helper/components-definer';

import '@vaadin/vaadin-app-layout/vaadin-app-layout';
import '@vaadin/vaadin-tabs/vaadin-tabs';
import '@vaadin/vaadin-tabs/vaadin-tab';

export default class AppLayout extends LitElement {
    constructor() {
        super();
    }
    static get name() {
        return 'app-layout';
    }
    static get styles() {
        return css `
        .content {
            padding: 50px;
        }

        h3 {
            text-align: center;
        }       
        a{
            text-decoration: none;
        }

        vaadin-tab{
            margin-top: 8px;
        }
        `;
    }
    static get properties() {
        return {
            myProp: String
        };
    }
    render() {
        return html `
            <vaadin-app-layout>
            <!-- <img slot="branding" referrerpolicy="no-referrer" src="http://www.pngall.com/wp-content/uploads/2/Share-PNG-Free-Download.png" alt="Share Logo" width="75"> -->
            <vaadin-tabs slot="menu">
                <a href="/">
                    <vaadin-tab theme="icon-on-top">
                        Sign In
                    </vaadin-tab>
                </a>
                <a href="/signup">
                    <vaadin-tab theme="icon-on-top">                
                        Sign Up
                    </vaadin-tab>
                </a>
                <a href="/feed">
                    <vaadin-tab theme="icon-on-top">                                    
                        Feed                    
                    </vaadin-tab>
                </a>
                <a href="/profile">
                    <vaadin-tab theme="icon-on-top">                                    
                            Profile                    
                    </vaadin-tab>
                </a>
            </vaadin-tabs>

            <div class="content">
                <slot></slot>
            </div>
            </vaadin-app-layout>
      `;
    }
}

defineComponent(AppLayout);