import { schema } from 'normalizr';

const entityOptions = {
  idAttribute : '_id'
};

const userEntity = new schema.Entity('user', entityOptions);
const postEntity = new schema.Entity('post', entityOptions);
const commentEntity = new schema.Entity('comment', entityOptions);
const likeEntity = new schema.Entity('like', entityOptions);

export const userSchema = {
  followers : [userEntity],
  following : [userEntity],
  posts : [postEntity]
};

export const postSchema = {
  user : userEntity,
  comments : [commentEntity],
  likes : [likeEntity]  
};

export const commentSchema = {
  user : userEntity  
};

export const likeSchema = {
  user : userEntity  
};